"use client";

import React, { useState, useEffect } from "react";
import { motion } from "framer-motion";
import { Search } from "lucide-react";

interface HeaderProps {
  searchQuery?: string;
  onSearchChange?: (query: string) => void;
}

const Header = ({ searchQuery = "", onSearchChange }: HeaderProps) => {
  const [currentTime, setCurrentTime] = useState(new Date());

  useEffect(() => {
    // Client-side only interval
    const timer = setInterval(() => setCurrentTime(new Date()), 1000);
    return () => clearInterval(timer);
  }, []);

  // Hydration mismatch prevention: only show time after mount or use consistent initial state
  // Simplest fix for clock hydration mismatch is to use formattedTime only after mount,
  // but for now we'll accept a potential blink or use suppressHydrationWarning on the specific element if Next.js allows.
  // Better pattern:
  const [mounted, setMounted] = useState(false);
  useEffect(() => setMounted(true), []);

  const formattedTime = currentTime.toLocaleTimeString("en-US", {
    hour: "2-digit",
    minute: "2-digit",
    second: "2-digit",
    hour12: true,
  });

  return (
    <header
      role="banner"
      className="
        flex items-center justify-between 
        px-4 md:px-8 
        /* Adjusted Padding: Ensure vertical centering by adding safe-area to base padding */
        pb-4 md:pb-6
        pt-[calc(1rem+env(safe-area-inset-top))] md:pt-[calc(1.5rem+env(safe-area-inset-top))]
        bg-[var(--glass-panel)] backdrop-blur-[var(--blur-strength)] sticky top-0 z-30 
        border-b border-[var(--glass-border)]
        /* Shadow: Consistent shadow-xl */
        shadow-xl
        transition-colors duration-300
        gap-4
      "
    >
      {/* LEFT: Title */}

      <motion.div
        initial={{ opacity: 0, x: -20 }}
        animate={{ opacity: 1, x: 0 }}
        transition={{ duration: 0.5, ease: "easeOut" }}
        className="flex flex-col justify-center shrink-0"
      >
        <h1 className="font-bold text-[var(--text-primary)] tracking-tight leading-none transition-colors duration-300">
          {/* Always visible 'Welcome to' with responsive sizing */}
          <span className="block text-xs sm:text-sm md:text-3xl lg:text-4xl opacity-60 font-medium mb-0.5 md:mb-1">
            Welcome to
          </span>
          <span className="text-xl sm:text-3xl md:text-5xl lg:text-6xl text-brand-primary block filter drop-shadow-md">
            QuickBite
          </span>
        </h1>
      </motion.div>

      {/* CENTER: SEARCH BAR */}
      <div className="flex-1 max-w-xl mx-4 hidden md:block">
        <div
          className="
            relative group
            bg-gray-100/50 dark:bg-white/5 
            border border-gray-200 dark:border-white/10
            focus-within:bg-white dark:focus-within:bg-white/10 
            focus-within:border-brand-primary/50 dark:focus-within:border-brand-primary/50
            focus-within:shadow-lg focus-within:shadow-brand-primary/10
            rounded-full transition-all duration-300 overflow-hidden
            flex items-center
         "
        >
          <div className="pl-4 pr-3 text-zinc-400 dark:text-gray-500 group-focus-within:text-brand-primary transition-colors">
            <Search size={22} strokeWidth={2.5} />
          </div>
          <input
            type="text"
            value={searchQuery}
            onChange={(e) => onSearchChange?.(e.target.value)}
            placeholder="Search for burgers, drinks..."
            className="
                  w-full py-4 bg-transparent outline-none border-none 
                  text-lg font-medium text-zinc-900 dark:text-white 
                  placeholder-zinc-400 dark:placeholder-gray-500
               "
          />
        </div>
      </div>

      {/* RIGHT: Actions */}
      {/* Theme Toggle & Live Clock Container */}
      <div className="flex items-center gap-4 md:gap-6">
        {/* Live Clock Section */}
        {/* Increased min-width to 100px/160px */}
        <div
          className="flex flex-col items-end min-w-[90px] md:min-w-[160px]"
          aria-live="polite"
        >
          <span className="text-lg md:text-4xl font-bold text-[var(--text-primary)] leading-none tracking-widest tabular-nums transition-colors duration-300">
            {mounted ? formattedTime : "--:-- --"}
          </span>

          <div className="flex items-center gap-1.5 md:gap-2 mt-1 md:mt-2">
            {/* Ping animation removed, only static dot remains */}
            <span className="relative flex h-2 w-2 md:h-3 md:w-3">
              <span className="relative inline-flex rounded-full h-full w-full bg-green-500"></span>
            </span>
            <span className="text-[10px] md:text-base text-brand-primary font-bold tracking-widest opacity-90">
              OPEN 24/7
            </span>
          </div>
        </div>
      </div>
    </header>
  );
};

export default Header;
